.reviews {
  min-width: 320px;
  margin: 0;
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;

  @media (min-width: 600px) {
    padding-bottom: 0px;
  }

  @media (min-width: 1200px) {
    width: 968px;
    margin: 60px auto 0;
    padding-top: 100px;
    background: url("../img/icon-quotes.svg") no-repeat center top;
    background-size: 79px 63px;
  }
}

.reviews__list {
  @media (min-width: 1200px) {
    position: relative;
  }
}

.reviews__item--nonactive {
  display: none;
}

.reviews__item--active {
  margin: 0;
  padding: 0 20px;

  @media (min-width: 660px) {
    display: flex;
    flex-direction: column-reverse;
  }
}

.reviews__author-name {
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  font-style: normal;
  color: #000;
  }

.reviews__author-info {
  display: none;

  @media (min-width: 660px) {
    display: block;
    margin: 10px 0 40px 0;
  }
}

.reviews__text {
  font-size: 14px;
  line-height: 30px;
  color: #283645;

  @media (min-width: 660px) {
    width: 620px;
    font-size: 18px;
    line-height: 30px;
    margin: 0 auto;
    padding-bottom: 40px;
  }
}
